html {
  background-color: gray;
  font-family: Roboto, sans-serif;
}

body {
  text-transform: uppercase;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
}

:root {
  --active-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-left: 17px;
  text-decoration: none;
  display: inline-flex;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: inline-flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  margin-left: 0;
  position: relative;
}

.is-active:hover {
  color: var(--active-color);
}

li:first-child {
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  align-self: flex-end;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
}

